
import { defineComponent } from "vue";

export default defineComponent({
    name: "Base64_Encode",
    data() {
        return {
            encoded: "",
            decoded: "",
        };
    },
    methods: {
        async decode() {
            if (this.encoded === "") {
                this.decoded = "Please fill in the Fields"
                return;
            }
            this.decoded = atob(this.encoded);
        },
    },
});
