
import { defineComponent } from "vue";

export default defineComponent({
    name: "Dec_Hex",
    data() {
        return {
            dec: "",
            hex: "",
        };
    },
    methods: {
        async encode() {
            if (this.dec === "") {
                this.hex = "Please fill in the Fields"
                return;
            }
            this.hex = Number(this.dec).toString(16);
        },
    },
});
