
import { defineComponent } from "vue";

export default defineComponent({
    name: "Hex_Dec",
    data() {
        return {
            dec: "",
            hex: "",
        };
    },
    methods: {
        async encode() {
            if (this.hex === "") {
                this.dec = "Please fill in the Fields"
                return;
            }
            this.dec = String(parseInt(this.hex, 16));
        },
    },
});
