
import { defineComponent } from "vue";

export default defineComponent({
    name: "Base64_Encode",
    data() {
        return {
            message: "",
            encoded: "",
        };
    },
    methods: {
        async encode() {
            if (this.message === "") {
                this.encoded = "Please fill in the Fields"
                return;
            }
            this.encoded = btoa(this.message);
        },
    },
});
